@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;900&display=swap);
* {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

body {
  background: #000;
  color: white;
}

.main {
  background-color: #107c10;
}

.login-container {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
  width: 100vw;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.login-box {
  background-color: #fff;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 50px;
  border-radius: 5px;
}

.title {
  font-weight: 100;
  font-size: 42px;
  margin-bottom: 50px;
  color: black;
}

.input-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.pid-input {
  height: 25px;
  border: none;
  background-color: #e6e6e6;
  padding: 5px 0 5px 5px;
  border-radius: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
  width: 100%;
}

.pid-input:focus {
  outline: none;
}

.submit-button {
  width: 100%;
  height: 35px;
  text-decoration: none;
  background-color: #107c10;
  color: #fff;
  text-align: center;
  padding: 7px;
  border-radius: 5px;
  float: right;
}

:root {
  --unit: 10px;
}

#overlay {
  position: absolute;
  /* display: none; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0);
  z-index: 2;
}

.container {
  height: 95vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.video-container {
  height: 100%;
  width: 100%;
  position: relative;
  margin-top: 10px;
}

.warning {
  padding-top: 15px;
  padding-bottom: 15px;
  color: #9bf00b;
}

.ticks {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 10px;
  width: 97%;
  margin: 0 auto;
  /* padding: var(--unit), var(--unit)*2; */
}

.tick {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 1px;
  background: grey;
  height: 10px;
  height: var(--unit);
  line-height: 35px;
  margin-bottom: 10px * 2;
  margin-bottom: var(--unit) * 2;
}

.sentiment-container {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.smile-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.slider-container {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-right: 10px;
  padding-left: 10px;
}

.slider {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 50px;
  background: #737373;
  /* background: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(125,255,72,1) 50%, rgba(0,166,39,1) 100%); */
  outline: none;
  opacity: 1;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 50px;
  height: 50px;
  background: #107c10;
  border: 2px solid #9bf00b;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #4caf50;
  border: 2px solid #9bf00b;
  cursor: pointer;
}

